  .checkbox {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: 0.75rem;
    text-align: left;
    margin-top: 1rem;
  }
  

  form {
    width: 95%;
    max-width: 300px;
    padding: 2rem 2rem 0.5rem 2rem;
    border-radius: 10px;
    background-color: #fff;
    border: 1px solid #ced4da;
    margin: 3% auto;
  }
  
  form label {
    font-size: 0.9rem;
    font-weight: bold;
    display: block;
    text-align: left;
    margin: 0.7rem 0.3rem 0.5rem;
  }
  
  input,
  select {
    width: 100%;
    padding: 0.5rem;
    font-size: 0.8rem;
    color: #000;
    border: 1px solid #ced4da;
    background-color: #fff;
    border-radius: 5px;
    outline: none;
  }
  
  input:focus,
  select:focus {
    border-color: #009EE2;
  }
  
  input::-webkit-input-placeholder, select::-webkit-input-placeholder {
    color: #4a5568;
  }
  
  input::placeholder,
  select::placeholder {
    color: #4a5568;
  }
  
  input[type="checkbox"] {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-right: 0.5rem;
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
    color: #000;
  }
  
  .button {
    display: block;
    margin: 1rem 0;
    padding: 0.35rem 1.5rem;
    background-color: #009EE2;
    color: #fff;
    border: none;
    width: auto;
    font-size: 1rem;
    font-weight: bold;
    cursor: pointer;
  }
  .button2 {
    display: block;
    margin: 1rem 0;
    padding: 0.35rem 0.5rem;
    background-color: #fff;
    text-decoration: underline;
    color: #009EE2;
    border: none;
    border-radius: 3px;
    width: auto;
    font-size: 1rem;
    font-weight: normal;
    cursor: pointer;
  }
  input.input-error,
  select.input-error {
    border-color: #fc8181;
  }
  
  button:disabled {
    opacity: 0.35;
  }
  
